"use client";

import { useState, useEffect } from "react";
import { useNews } from "@/context/newsContext";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Label } from "@/components/ui/label";
import { toast } from "sonner";
import { saveGeminiApiKey } from "@/lib/gemini";

export default function AnimeNewsPage() {
  const { news, isLoading, error, fetchNews, updateNews, refreshNews } = useNews();
  const [editingId, setEditingId] = useState<string | null>(null);
  const [editForm, setEditForm] = useState({ title: "", description: "" });
  const [isApiKeyDialogOpen, setIsApiKeyDialogOpen] = useState(false);
  const [apiKey, setApiKey] = useState("");

  // Carica le notizie al mount se non ci sono
  useEffect(() => {
    if (news.length === 0 && !isLoading) {
      fetchNews();
    }
  }, [news.length, isLoading, fetchNews]);

  const handleRefresh = async () => {
    try {
      await refreshNews();
      toast.success("Notizie aggiornate!");
    } catch (error) {
      toast.error("Errore nell'aggiornamento delle notizie");
    }
  };

  const handleEdit = (newsItem: any) => {
    setEditingId(newsItem.id);
    setEditForm({
      title: newsItem.title,
      description: newsItem.description,
    });
  };

  const handleSave = () => {
    if (!editingId) return;
    
    if (!editForm.title.trim() || !editForm.description.trim()) {
      toast.error("Titolo e descrizione sono obbligatori");
      return;
    }

    updateNews(editingId, {
      title: editForm.title,
      description: editForm.description,
    });
    
    setEditingId(null);
    setEditForm({ title: "", description: "" });
    toast.success("Notizia salvata!");
  };

  const handleCancel = () => {
    setEditingId(null);
    setEditForm({ title: "", description: "" });
  };

  const handleSaveApiKey = () => {
    if (!apiKey.trim()) {
      toast.error("Inserisci una chiave API valida");
      return;
    }
    
    saveGeminiApiKey(apiKey);
    setIsApiKeyDialogOpen(false);
    setApiKey("");
    toast.success("Chiave API salvata!");
  };

  const formatDate = (dateString: string) => {
    try {
      const date = new Date(dateString);
      return date.toLocaleDateString('it-IT', {
        day: 'numeric',
        month: 'long',
        year: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    } catch {
      return dateString;
    }
  };

  return (
    <div className="max-w-7xl mx-auto">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold text-foreground mb-2">Notizie Anime</h1>
          <p className="text-muted-foreground">
            Le ultime notizie dal mondo degli anime
          </p>
        </div>
        
        <div className="flex gap-2 mt-4 sm:mt-0">
          <Dialog open={isApiKeyDialogOpen} onOpenChange={setIsApiKeyDialogOpen}>
            <DialogTrigger asChild>
              <Button variant="outline">
                Configura API
              </Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>Configura Chiave API Gemini</DialogTitle>
                <DialogDescription>
                  Inserisci la tua chiave API Gemini per ottenere notizie aggiornate
                </DialogDescription>
              </DialogHeader>
              
              <div className="space-y-4">
                <div>
                  <Label htmlFor="apiKey">Chiave API Gemini</Label>
                  <Input
                    id="apiKey"
                    type="password"
                    value={apiKey}
                    onChange={(e) => setApiKey(e.target.value)}
                    placeholder="AIzaSy..."
                  />
                </div>
                
                <div className="flex gap-2">
                  <Button onClick={handleSaveApiKey} className="flex-1">
                    Salva
                  </Button>
                  <Button variant="outline" onClick={() => setIsApiKeyDialogOpen(false)}>
                    Annulla
                  </Button>
                </div>
              </div>
            </DialogContent>
          </Dialog>
          
          <Button onClick={handleRefresh} disabled={isLoading}>
            {isLoading ? "Caricamento..." : "Aggiorna"}
          </Button>
        </div>
      </div>

      {/* Error State */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-6">
          <p className="text-red-800">{error}</p>
          <Button 
            variant="outline" 
            size="sm" 
            onClick={handleRefresh}
            className="mt-2"
          >
            Riprova
          </Button>
        </div>
      )}

      {/* Loading State */}
      {isLoading && news.length === 0 && (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {[...Array(6)].map((_, i) => (
            <Card key={i} className="animate-pulse">
              <CardHeader>
                <div className="h-4 bg-gray-200 rounded w-3/4 mb-2"></div>
                <div className="h-3 bg-gray-200 rounded w-1/2"></div>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  <div className="h-3 bg-gray-200 rounded"></div>
                  <div className="h-3 bg-gray-200 rounded"></div>
                  <div className="h-3 bg-gray-200 rounded w-2/3"></div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}

      {/* News Grid */}
      {!isLoading && news.length > 0 && (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {news.map((newsItem) => (
            <Card key={newsItem.id} className="hover:shadow-lg transition-shadow">
              <CardHeader>
                {editingId === newsItem.id ? (
                  <div className="space-y-2">
                    <Input
                      value={editForm.title}
                      onChange={(e) => setEditForm(prev => ({ ...prev, title: e.target.value }))}
                      placeholder="Titolo della notizia"
                    />
                  </div>
                ) : (
                  <>
                    <CardTitle className="line-clamp-2 text-lg">
                      {newsItem.title}
                    </CardTitle>
                    <CardDescription className="flex items-center justify-between">
                      <Badge variant="secondary" className="text-xs">
                        {newsItem.source}
                      </Badge>
                      <span className="text-xs text-muted-foreground">
                        {formatDate(newsItem.publishedAt)}
                      </span>
                    </CardDescription>
                  </>
                )}
              </CardHeader>
              
              <CardContent>
                {editingId === newsItem.id ? (
                  <div className="space-y-4">
                    <Textarea
                      value={editForm.description}
                      onChange={(e) => setEditForm(prev => ({ ...prev, description: e.target.value }))}
                      placeholder="Descrizione della notizia"
                      rows={4}
                    />
                    <div className="flex gap-2">
                      <Button onClick={handleSave} size="sm" className="flex-1">
                        Salva
                      </Button>
                      <Button onClick={handleCancel} variant="outline" size="sm" className="flex-1">
                        Annulla
                      </Button>
                    </div>
                  </div>
                ) : (
                  <>
                    <p className="text-sm text-muted-foreground line-clamp-4 mb-4">
                      {newsItem.description}
                    </p>
                    
                    <div className="flex gap-2">
                      <Button 
                        onClick={() => handleEdit(newsItem)} 
                        variant="outline" 
                        size="sm"
                        className="flex-1"
                      >
                        Modifica
                      </Button>
                      {newsItem.url && newsItem.url !== "https://example.com/news/1" && (
                        <Button 
                          variant="outline" 
                          size="sm"
                          onClick={() => window.open(newsItem.url, '_blank')}
                          className="flex-1"
                        >
                          Leggi
                        </Button>
                      )}
                    </div>
                  </>
                )}
              </CardContent>
            </Card>
          ))}
        </div>
      )}

      {/* Empty State */}
      {!isLoading && news.length === 0 && !error && (
        <div className="text-center py-16">
          <h3 className="text-xl font-semibold mb-2">Nessuna Notizia Disponibile</h3>
          <p className="text-muted-foreground mb-6">
            Clicca su "Aggiorna" per caricare le ultime notizie anime
          </p>
          <Button onClick={handleRefresh}>
            Carica Notizie
          </Button>
        </div>
      )}
    </div>
  );
}
